import scss from "./Checkbox.module.scss";
import { CheckboxProps } from "./Checkbox.types";
import minecraftClickSound from "../../assets/minecraft_click.mp3";

export const Checkbox = (props: CheckboxProps) => {
  const clickSound = new Audio(minecraftClickSound);
  
  function handleCheckbox() {
    clickSound.play();
    props.setChecked((prev) => !prev);
  }

  return (
    <label>
      {props.label}
      <input checked={props.checked} onChange={handleCheckbox} className={scss.checkbox} type="checkbox" />
    </label>
  );
};
